import React, { Component, Fragment } from 'react';
import { Table, Form, Input, Button } from 'antd';
import { useHistory } from 'react-router-dom';
import swal from 'sweetalert';

import axios from 'axios';

const columns = [
    {
        title: 'Artis Name',
        dataIndex: 'title',
        key: 'title',
        render: text => <a>{text}</a>,
    },
    {
        title: 'Jumlah Album',
        dataIndex: 'album',
        key: 'album',
    },
    {
        title: 'Jumlah Lagu',
        dataIndex: 'lagu',
        key: 'lagu',
    },
];

class RegistrationForm extends React.Component {
    state = {
        data: [],
    };

    componentDidMount() {
        this.getData();
    }

    getData = () => {
        axios
            .get('/api/artist')
            .then(res => {
                // console.log('admob data', res.data);

                this.setState({
                    data: res.data,
                });
            })
            .catch(err => {
                swal('Sorry!', 'error Load Data', 'error');
                console.log(err);
            });
    };

    handleSubmit = e => {
        const { data } = this.state;
        // console.log('id', data._id);
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                axios
                    .post(`/api/artist/`, values)
                    .then(res => {
                        this.getData();
                        swal(
                            'Good job!',
                            `Artis ${res.data.name} telah ditambahkan!`,
                            'success',
                        );
                        console.log('respon', res);
                    })
                    .catch(err => {
                        console.log('error update', err);
                    });
                console.log('values: ', values);
            }
        });
    };

    render() {
        const { getFieldDecorator } = this.props.form;

        const { data } = this.state;
        console.log('ini data baru', data);
        const newData = data.map((item, index) => {
            return {
                key: index,
                title: item.name,
                album: item.album.length,
                lagu: item.music.length,
            };
        });

        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 8 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 16,
                    offset: 8,
                },
            },
        };

        return (
            <div>
                <h1 style={{ textAlign: 'center', marginBottom: 20 }}>
                    Artis Page
                </h1>
                <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                    <Form.Item label="Artis Name">
                        {getFieldDecorator('name')(
                            <Input placeholder=" Add New Artis" />,
                        )}
                    </Form.Item>

                    <Form.Item {...tailFormItemLayout}>
                        <Button type="primary" htmlType="submit">
                            Add New Artis
                        </Button>
                    </Form.Item>
                </Form>
                <Table bordered={true} columns={columns} dataSource={newData} />
            </div>
        );
    }
}

const WrappedRegistrationForm = Form.create({ name: 'register' })(
    RegistrationForm,
);

export class MusicPage extends Component {
    render() {
        return (
            <Fragment>
                <WrappedRegistrationForm />
            </Fragment>
        );
    }
}

export default MusicPage;
