import React, { Component, Fragment } from 'react';
import { Form, Input, Button } from 'antd';
import axios from 'axios';

class RegistrationForm extends React.Component {
    state = {
        confirmDirty: false,

        edit: true,
        data: [],
    };

    componentDidMount() {
        this.getData();
    }

    getData = () => {
        axios
            .get('/api/admob')
            .then(res => {
                console.log('admob data', res.data[0].admobID);
                this.setState({
                    data: res.data[0],
                });
            })
            .catch(err => {
                console.log(err);
            });
    };

    handleSubmit = e => {
        const { data } = this.state;
        console.log('id', data._id);
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                axios
                    .put(`/api/admob/update/${data._id}`, values)
                    .then(res => {
                        console.log('succes', res);
                        this.getData();
                    })
                    .catch(err => {
                        console.log('error update', err);
                    });
                console.log('Received values of form: ', values);
            }
        });
    };

    render() {
        const { getFieldDecorator } = this.props.form;
        // console.log('ini data baru', this.state.data);
        const { data } = this.state;

        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 8 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 16,
                    offset: 8,
                },
            },
        };

        return (
            <Fragment>
                <div>
                    <h1 style={{ textAlign: 'center', marginBottom: 20 }}>
                        Admob Page
                    </h1>
                    <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                        <Form.Item label="Admob Id">
                            {getFieldDecorator('admobID')(
                                <Input placeholder={data.admobID} />,
                            )}
                        </Form.Item>
                        <Form.Item label="Admob Aplikasi">
                            {getFieldDecorator('admobAplikasi')(
                                <Input placeholder={data.admobAplikasi} />,
                            )}
                        </Form.Item>
                        <Form.Item label="Admob Banner">
                            {getFieldDecorator('admobBanner')(
                                <Input placeholder={data.admobBanner} />,
                            )}
                        </Form.Item>
                        <Form.Item label="Admob Intertial">
                            {getFieldDecorator('admobIDIntersial')(
                                <Input placeholder={data.admobIDIntersial} />,
                            )}
                        </Form.Item>
                        <Form.Item label="Admob Reward">
                            {getFieldDecorator('admobIDReward')(
                                <Input placeholder={data.admobIDReward} />,
                            )}
                        </Form.Item>

                        <Form.Item {...tailFormItemLayout}>
                            <Button type="primary" htmlType="submit">
                                Update
                            </Button>
                        </Form.Item>
                    </Form>
                </div>
            </Fragment>
        );
    }
}

const WrappedRegistrationForm = Form.create({ name: 'register' })(
    RegistrationForm,
);

export class AdmobPage extends Component {
    render() {
        return <WrappedRegistrationForm />;
    }
}

export default AdmobPage;
