import React, { Component, Fragment } from 'react';
import axios from 'axios';
import { Input, Button, Empty, Tag } from 'antd';
import swal from 'sweetalert';

export class AlbumPageDetail extends Component {
    state = {
        dataAlbum: [],
        dataMusik: [],
        addSong: '',
        idSong: '',
    };
    componentDidMount() {
        this.getDataArtist();
        this.getDataAlbum();
    }

    getDataAlbum = () => {
        const albumId = this.props.match.params._id;
        axios
            .get(`/api/album/${albumId}`)
            .then(res => {
                this.setState({
                    dataAlbum: res.data,
                });
                console.log('ini album', res.data);
            })
            .catch(err => console.log(err));
    };

    getDataArtist = () => {
        const artistId = this.props.match.params._album;
        axios
            .get(`/api/artist/${artistId}`)
            .then(res => {
                this.setState({
                    dataMusik: res.data.music,
                });
                console.log('ini artist', res.data.music);
            })
            .catch(err => console.log(err));
    };

    addMusikToAlbum = () => {
        const { idSong } = this.state;
        const albumId = this.props.match.params._id;
        // alert('ini id musik' + idSong + 'dan ini id album' + albumId);
        axios
            .post(
                `/api/album/assign/music/${albumId}/${idSong}`,
            )
            .then(res => {
                swal('Good job!', `berhasil menambahkan lagu`, 'success');
                this.getDataAlbum();
                this.setState({ idSong: '', addSong: '' });
            })
            .catch(err => {
                swal('error!', `gagal menambahkan lagu`, 'error');
                console.log('ini error menambahkan lagu', err);
            });
    };
    render() {
        const { dataAlbum, dataMusik } = this.state;
        console.log('album song', dataAlbum.albumSong);
        console.log('musik', dataMusik);
        // console.log('apa propsnya id artis', this.props.match.params._id);
        return (
            <Fragment>
                <Tag
                    style={{
                        paddingTop: 5,
                        paddingBottom: 5,
                        paddingLeft: 25,
                        paddingRight: 25,
                        marginBottom: 10,
                    }}
                    color="magenta"
                >
                    album Page Detail
                </Tag>

                <br />
                <p>
                    Nama Album :{' '}
                    {dataAlbum.albumName !== undefined && dataAlbum.albumName}
                </p>
                <p>
                    Jumlah Lagu :{' '}
                    {dataAlbum.albumSong !== undefined &&
                        dataAlbum.albumSong.length}{' '}
                    of song
                </p>

                <Tag
                    style={{
                        paddingTop: 5,
                        paddingBottom: 5,
                        paddingLeft: 25,
                        paddingRight: 25,
                        marginBottom: 10,
                        marginTop: 40,
                    }}
                    color="magenta"
                >
                    tambahkan Lagu :
                </Tag>

                <Input
                    style={{ marginBottom: 20 }}
                    placeholder="Tambahkan Lagu"
                    value={this.state.addSong}
                    // onChange={event =>
                    //     this.setState({
                    //         albumImage: event.target.value,
                    //     })
                    // }
                />
                <Button
                    style={{ marginBottom: 20 }}
                    type="primary"
                    onClick={this.addMusikToAlbum}
                >
                    Submit
                </Button>
                <div
                    style={{
                        background: 'grey',
                        padding: 5,
                        paddingLeft: 30,
                    }}
                >
                    <p
                        style={{
                            color: 'white',
                            fontSize: 16,
                            fontWeight: 'bold',
                        }}
                    >
                        List Lagu yang Sekarang :
                    </p>
                </div>

                {dataAlbum.albumSong !== undefined &&
                dataAlbum.albumSong.length >= 0 ? (
                    dataAlbum.albumSong.map((item, index) => {
                        return (
                            <Fragment>
                                <div
                                    style={{
                                        // background: 'yellow',
                                        marginBottom: 10,
                                        padding: 5,
                                        paddingLeft: 30,
                                    }}
                                    key={index}
                                >
                                    {item.title}
                                </div>
                            </Fragment>
                        );
                    })
                ) : (
                    <Empty style={{ marginTop: 20 }} />
                )}

                <div
                    style={{
                        background: 'grey',
                        padding: 5,
                        paddingLeft: 30,
                        marginTop: 50,
                    }}
                >
                    <p
                        style={{
                            color: 'white',
                            fontSize: 16,
                            fontWeight: 'bold',
                        }}
                    >
                        Daftar List Lagu
                    </p>
                </div>
                {dataMusik.map((item, index) => {
                    return (
                        <Fragment>
                            <div
                                onClick={() => {
                                    this.setState({
                                        addSong: item.title,
                                        idSong: item._id,
                                    });
                                }}
                                style={{
                                    // background: 'yellow',
                                    marginBottom: 10,
                                    padding: 5,
                                    paddingLeft: 30,
                                }}
                                key={index}
                            >
                                {item.title}
                            </div>
                        </Fragment>
                    );
                })}
            </Fragment>
        );
    }
}

export default AlbumPageDetail;
