import React, { Component, Fragment } from 'react';
import './HomePage.css';
import { Layout, Menu, Breadcrumb, Icon } from 'antd';
import { Switch, Route, Link } from 'react-router-dom';
import ArtisPage from '../ArtisPage';
import ArtisPageDetail from '../ArtisPageDetail';
import AdmobPage from '../AdmobPage';
import AlbumPageDetail from '../AlbumPageDetail';
import Axios from 'axios';
import { BrowserRouter as Router, useHistory } from 'react-router-dom';

const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;

export class HomePage extends Component {
    state = {
        collapsed: false,
        data: [],
    };

    onCollapse = collapsed => {
        console.log(collapsed);
        this.setState({ collapsed });
    };

    componentDidMount() {
        this.getData();
    }

    getData = () => {
        Axios.get('/api/artist')
            .then(res => {
                // console.log('dataHome', res.data);
                this.setState({
                    data: res.data,
                });
            })
            .catch(err => console.log(err));
    };
    render() {
        const { data } = this.state;
        return (
            <Router history={useHistory}>
                <Layout style={{ minHeight: '100vh' }}>
                    <Sider
                        collapsible
                        collapsed={this.state.collapsed}
                        onCollapse={this.onCollapse}
                    >
                        <div className="logo" />
                        <Menu
                            theme="dark"
                            defaultSelectedKeys={['1']}
                            mode="inline"
                        >
                            <Menu.Item key="-1">
                                <Link to="/">
                                    <Icon type="android" />
                                    <span>Admob</span>
                                </Link>
                            </Menu.Item>

                            <SubMenu
                                key="-2"
                                title={
                                    <span>
                                        <Link to="/music">
                                            <Icon type="user" />
                                            <span>Artist</span>
                                        </Link>
                                    </span>
                                }
                            >
                                {data.map((item, index) => {
                                    return (
                                        <Menu.Item key={index}>
                                            <Link
                                                to={`/artis_detail/${item._id}`}
                                            >
                                                <Icon type="bug" />
                                                <span>{item.name}</span>
                                            </Link>
                                        </Menu.Item>
                                    );
                                })}
                            </SubMenu>
                        </Menu>
                    </Sider>
                    <Layout>
                        <Header style={{ background: '#fff', padding: 0 }}>
                            <div
                                style={{
                                    marginLeft: 20,
                                    textAlign: 'center',

                                    fontSize: 20,
                                }}
                            >
                                <h1>ADMIN PAGE of MUSIC APP</h1>
                            </div>
                        </Header>
                        <Content style={{ margin: '0 16px' }}>
                            <Breadcrumb style={{ margin: '16px 0' }}>
                                {/* <Breadcrumb.Item>Admob</Breadcrumb.Item> */}
                            </Breadcrumb>
                            <div
                                style={{
                                    padding: 24,
                                    background: '#fff',
                                    minHeight: 360,
                                }}
                            >
                                <Switch>
                                    <Route path="/" exact>
                                        <AdmobPage />
                                    </Route>

                                    <Route path="/music" component={ArtisPage}>
                                        {/* <ArtisPage /> */}
                                    </Route>
                                    <Route
                                        path="/artis_detail/:_id"
                                        component={ArtisPageDetail}
                                    ></Route>
                                    <Route
                                        path="/album_detail/:_id/:_album"
                                        component={AlbumPageDetail}
                                    >
                                        {/* <AlbumPageDetail /> */}
                                    </Route>
                                </Switch>
                            </div>
                        </Content>
                        <Footer style={{ textAlign: 'center' }}>
                            Admin Page Music App 2020 Created by Robert
                            Yulisman
                        </Footer>
                    </Layout>
                </Layout>
            </Router>
        );
    }
}

export default HomePage;
