import React, { Component } from 'react';
import axios from 'axios';
import { Row, Col } from 'antd';
import './ArtisPageDetail.css';
import {
    Card,
    Modal,
    Form,
    Select,
    Input,
    Button,
    Table,
    InputNumber,
    Popconfirm,
    Divider,
    Tag,
    Collapse,
    Empty,
} from 'antd';
import swal from 'sweetalert';

const { Panel } = Collapse;
function callback(key) {
    console.log(key);
}

const text = `
  A dog is a type of domesticated animal.
  Known for its loyalty and faithfulness,
  it can be found as a welcome guest in many households across the world.
`;

const { TextArea } = Input;
const { Meta } = Card;
const columns = [
    {
        title: 'Judul Lagu',
        dataIndex: 'judul',
        key: 'judul',
        render: text => <a>{text}</a>,
    },
    {
        title: 'Link Lagu',
        dataIndex: 'link',
        key: 'link',
    },
    {
        title: 'Lirik Lagu',
        dataIndex: 'lirik',
        key: 'lirik',
    },
];

// ini table batas atas

// ini table batas bawah

export class ArtisPageDetail extends Component {
    state = {
        data: [],
        ModalText: 'Content of the modal',
        visible: false,
        confirmLoading: false,
        albumName: '',
        albumImage: '',
        title: '',
        audioUrl: '',
        lyric: '',
    };

    SubmitLaguBaru = () => {
        const { data, title, audioUrl, lyric } = this.state;
        axios
            .post(`/api/music/${data._id}`, {
                title,
                audioUrl,
                lyric,
            })
            .then(res => {
                this.setState({
                    title: '',
                    audioUrl: '',
                    lyric: '',
                });
                swal(
                    'Good job!',
                    `Lagu untuk ${res.data.name} telah ditambahkan!`,
                    'success',
                );
                console.log('inininininin', res);
                this.getDataArtis();
            })
            .catch(err =>
                swal(
                    'something Wrong!',
                    `error menambah kan lagu!, silahkan coba lagi`,
                    'error',
                ),
            );

        this.setState({
            ModalText: 'The modal will be closed after two seconds',
            confirmLoading: true,
        });
    };

    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = () => {
        const { data, albumImage, albumName } = this.state;
        axios
            .post(`/api/album/${data._id}`, {
                albumImage,
                albumName,
            })
            .then(res => {
                this.getDataArtis();
                swal('Good job!', `berhasil menambahkan Album baru`, 'success');
            })
            .catch(err => alert('respon error: ', err));

        this.setState({
            ModalText: 'The modal will be closed after two seconds',
            confirmLoading: true,
        });
        setTimeout(() => {
            this.setState({
                visible: false,
                confirmLoading: false,
            });
        }, 2000);
    };

    handleCancel = () => {
        console.log('Clicked cancel button');
        this.setState({
            visible: false,
        });
    };

    componentDidMount() {
        this.getDataArtis();
    }

    getDataArtis = () => {
        const id = this.props.match.params._id;
        console.log('ini id terbaru', id);
        axios
            .get(`/api/artist/${id}`)
            .then(res => {
                this.setState({
                    data: res.data,
                });
            })
            .catch(err => console.log(err));
    };
    render() {
        const {
            visible,
            confirmLoading,
            ModalText,
            data,
            title,
            audioUrl,
            lyric,
        } = this.state;
        console.log('ggagagga', data.music);

        const dataTable =
            data.music !== undefined &&
            data.music.map((item, index) => {
                return {
                    key: index,
                    judul: item.title,
                    link: item.audioUrl !== '' ? 'true' : 'url kosong',
                    lirik: item.lyric !== '' ? 'true' : 'tidak ada lirik',
                };
            });

        return (
            <div>
                <Tag
                    style={{
                        paddingTop: 10,
                        paddingBottom: 10,
                        paddingLeft: 15,
                        paddingRight: 15,
                    }}
                    color="#87d068"
                >
                    Detail Page for {data.name}
                </Tag>

                <br />
                <br />
                <Collapse defaultActiveKey={['1']} onChange={callback}>
                    <Panel header="Tambahkan Lagu Baru" key="1">
                        <div>
                            <Form>
                                <Input
                                    placeholder="Judul Lagu"
                                    allowClear
                                    value={title}
                                    onChange={even =>
                                        this.setState({
                                            title: even.target.value,
                                        })
                                    }
                                />
                                <br />
                                <br />
                                <Input
                                    placeholder="Url Lagu"
                                    allowClear
                                    value={audioUrl}
                                    onChange={even =>
                                        this.setState({
                                            audioUrl: even.target.value,
                                        })
                                    }
                                />
                                <br />
                                <br />
                                <TextArea
                                    placeholder="Lyric Lagu"
                                    allowClear
                                    value={lyric}
                                    onChange={even =>
                                        this.setState({
                                            lyric: even.target.value,
                                        })
                                    }
                                />
                                <br />
                                <br />
                                <Button
                                    onClick={this.SubmitLaguBaru}
                                    type="primary"
                                >
                                    Tambahkan
                                </Button>
                            </Form>
                        </div>
                    </Panel>
                    <Panel header="List Lagu" key="2">
                        <Table
                            bordered={true}
                            columns={columns}
                            dataSource={dataTable}
                        />
                    </Panel>
                    <Panel header="List Album" key="3">
                        <div className="gutter-example">
                            {data.album !== undefined &&
                            data.album.length < 0 ? (
                                <Empty
                                    image="https://gw.alipayobjects.com/mdn/miniapp_social/afts/img/A*pevERLJC9v0AAAAAAAAAAABjAQAAAQ/original"
                                    imageStyle={{
                                        height: 60,
                                    }}
                                    description={
                                        <span>Album Masih Kosong</span>
                                    }
                                >
                                    <Button
                                        onClick={this.showModal}
                                        type="primary"
                                    >
                                        Buat Album Pertama
                                    </Button>
                                </Empty>
                            ) : (
                                <Row
                                    gutter={[
                                        { xs: 8, sm: 16, md: 24, lg: 32 },
                                        20,
                                    ]}
                                >
                                    {data.album !== undefined &&
                                        data.album.map((item, index) => {
                                            return (
                                                <Col
                                                    key={index}
                                                    className="gutter-row"
                                                    span={6}
                                                    onClick={() => {
                                                        this.props.history.push(
                                                            `/album_detail/${item._id}/${data._id}`,
                                                        );
                                                        // alert(
                                                        //     'album id',
                                                        //     item._id,
                                                        // );
                                                    }}
                                                >
                                                    <Card
                                                        hoverable
                                                        style={{ width: 240 }}
                                                        cover={
                                                            <img
                                                                alt="example"
                                                                src={
                                                                    item.albumImage
                                                                }
                                                            />
                                                        }
                                                    >
                                                        <Meta
                                                            title={
                                                                item.albumName
                                                            }
                                                            description={`${item.albumSong.length} of songs`}
                                                        />
                                                    </Card>
                                                </Col>
                                            );
                                        })}
                                    <Col className="gutter-row" span={6}>
                                        <Card
                                            onClick={this.showModal}
                                            hoverable
                                            style={{ width: 240 }}
                                            cover={
                                                <img
                                                    alt="example"
                                                    src="https://static.thenounproject.com/png/396915-200.png"
                                                />
                                            }
                                        >
                                            <Meta
                                                title="add new Album"
                                                description="o of songs"
                                            />
                                        </Card>
                                    </Col>
                                </Row>
                            )}

                            <div>
                                <Modal
                                    title="Add Album"
                                    visible={visible}
                                    onOk={this.handleOk}
                                    confirmLoading={confirmLoading}
                                    onCancel={this.handleCancel}
                                >
                                    <Input
                                        placeholder="Album Name"
                                        value={this.state.albumName}
                                        onChange={event =>
                                            this.setState({
                                                albumName: event.target.value,
                                            })
                                        }
                                    />

                                    <Input
                                        style={{ marginTop: 20 }}
                                        placeholder="Album Image"
                                        value={this.state.albumImage}
                                        onChange={event =>
                                            this.setState({
                                                albumImage: event.target.value,
                                            })
                                        }
                                    />
                                </Modal>
                            </div>
                        </div>
                    </Panel>
                </Collapse>
            </div>
        );
    }
}

export default ArtisPageDetail;
